.form {
  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.form__filters-wrapper {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 520px;
  z-index: 100000;
  background-color: $white;
  padding-top: 16px;
  padding-bottom: 16px;
 
  &--open {
    display: block;
  }

  @media (min-width: $desktop-width) {
    z-index: 100;
    display: flex;
    align-items: center;
    gap: 32px;
    position: static;
    padding: 0;
  }
}

.form__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.form__close-button {
  position: absolute;
  top: 20px;
  right: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.form__buttons-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

.form__button {
  border: none;
  background-color: transparent;
  padding: 0;
  font-family: "Manrope", Arial, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.5px;
  cursor: pointer;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  gap: 8px;
  color: inherit;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 26px;

    &--open-filters {
      display: none;
    }
  }
}