.radio {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.radio__description {
  order: 2;
  flex-shrink: 0;
}

.radio__mark {
  order: 1;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  outline: 2px solid $black;
  flex-shrink: 0;
}

.radio__input:checked + .radio__mark {
  outline-color: $green-hover;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: $green-hover;
  }
}
