.warehouse-card {
  border-radius: 20px;
  background-color: $light-grey;
  padding: 16px;
  position: relative;

  @media (min-width: $desktop-width) {
    padding: 32px;
    display: grid;
    grid-template-columns: 179px auto 260px;
    gap: 24px;
  }
}

.warehouse-card__img {
  width: 71px;
  height: 48px;
  position: absolute;
  top: 16px;
  left: 16px;
  border-radius: 8px;

  @media (min-width: $desktop-width) {
    position: static;
    width: 179px;
    height: 102px;
    margin: 0 0 auto 0;
  }
}

.warehouse-card__container {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: column;
  }
}

.warehouse-card__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  margin-left: 80px;
  min-height: 52px;

  @media (min-width: $desktop-width) {
    margin: 0;
    min-height: auto;
    font-size: 22px;
    line-height: 35px;
  }
}

.warehouse-card__address {
  font-family: "Manrope", Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.5px;
  display: block;
  margin-bottom: 16px;
  color: $grey;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.warehouse-card__features-list {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.warehouse-card__features-item {
  font-family: "Manrope", Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  letter-spacing: -0.5px;
  padding: 4px 12px;
  border-radius: 16px;
  border: 1px solid $border-grey;
  background-color: $white;

  @media (min-width: $desktop-width) {
    font-size: 14px;
    line-height: 20px;
  }
}

.warehouse-card__wrapper {
  display: flex;
  gap: 32px;
  align-items: baseline;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    order: 2;
    margin: 0;
    gap: 10px;
    justify-content: space-between;
  }
}

.warehouse-card__available {
  font-family: "Manrope", Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.warehouse-card__price {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.warehouse-card__link {
  padding: 11px 16px;
  border-radius: 12px 32px 32px 12px;
  background-color: $background-dark-green;
  display: block;
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: -0.5px;
  position: relative;

  &:hover {
    background-color: $green-hover;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 32px;
    width: 65px;
    height: 14px;
    background-image: url("../images/svg/intro-arrow.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    order: 1;
    margin-bottom: auto;
  }
}
