.checkbox {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.checkbox__description {
  order: 2;
  flex-shrink: 0;
}

.checkbox__mark {
  order: 1;
  width: 18px;
  height: 18px;
  border-radius: 4px;
  outline: 2px solid $black;
  flex-shrink: 0;
}

.checkbox__input:checked + .checkbox__mark {
  outline-color: $green-hover;
  background-image: url("../images/svg/filters-tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.checkbox__schedule,
.checkbox__round-the-clock {
  display: block;
  font-family: "Manrope", Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
  letter-spacing: -0.5px;
  padding-left: 28px;
  color: $green-hover;
}

.checkbox__schedule {
  color: $background-purple;
}
